#vertical-line {
    border-right: 6px solid #ffcb00;
    height: auto;
    /* box-shadow: 3px 2px #0059ff6d; */
    margin-top: 100px;
    margin-bottom: 100px;
    border-radius: 15px;

}

@media  (max-width: 768px) {
    #vertical-line {
        order:2;
         
        align-items: center;      
        border-right: none;
        border-bottom: 6px solid #ffcb00;
        height: 10px; /* adjust height as needed */
        margin-left: 20px auto; /* adjust margin as needed */
        margin-right: 20px auto; /* adjust margin as needed */
    }
}
.content-container {

    margin: 0 auto;
}

.heading-container {
    text-align: center;
    margin-bottom: 30px;
}

.rti-container {
    display: flex;
    /* Set display to flex */
}

.hindi-rti {
    flex: 1;
    /* Take up 1/2 of the available space */

}

.english-rti {
    flex: 1;
    /* Take up 1/2 of the available space */

}

/* Media query for small screens */
@media (max-width: 768px) {
    .rti-container {
        flex-direction: column;
        /* Set flex-direction to column */
    }

    div.hindi-rti {
        order: 1;
        /* Change the order of the Hindi div */
        padding: 10px;
        margin: 5px auto;
        width: 80%
    }

    div.english-rti {
        order: 3;
        /* Change the order of the English div */
        padding: 10px;
        margin: 5px auto;
        width: 80%
    }  
}