table {
    border-collapse: collapse;
    width: 95%;
    margin: auto;
    margin-bottom: 100px;
    border-color: #fff;
    border-bottom: 1px solid #1b3058;
    
  }
  
  th, td {
    text-align: left;
    padding: 8px;
  }
  
  th {
    background-color: #ddd;
  }
  
  tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
