  .togglebtn{
    position: relative;
    margin: 12px;
    right: 0px;
    padding: 5px;
    background-color: #1b3058;
    border-radius: 8px;
    color: white;
    width: 200px;
  }
  #searchInput {
  border: none;
  outline: none;
  width: 95%;
  margin-left: 2.6%;
  margin-bottom: 20px;
}

/* When input gains focus, add a blue border below it */

#searchInput:focus {
